export default {
    namespaced: true, 
    state:{
        userID:"",//用户ID
        islogin:false,//是否登录
        isregister:false,//是否注册
        login_input_error:"",//登录输入错误提醒
        num:1,
        register_input_error:"",//注册输入错误提醒
        payment:[{//点劵充值面板数组
            coupons:"30",
            money:"30元"
        },{
            coupons:"50",
            money:"50元"
        },{
            coupons:"100",
            money:"100元"
        },{
            coupons:"150",
            money:"150元"
        },{
            coupons:"200",
            money:"200元"
        },{
            coupons:"300",
            money:"300元"
        },],
        user:[//用户信息数组
            {
                account:"admin",
                username:"admin",
                password: "123456",
                coupons:"0"
            },
        ]
    },
    mutations:{
        // 登录功能
        login(state,obj){
            if(obj.account=="" || obj.password==""){
                state.login_input_error="账号或密码不能为空";
            }else{
                state.user.forEach((item,index)=>{
                    if(item.account==obj.account&&item.password==obj.password){
                        state.userID=index;
                        state.islogin=true;
                    }
                });
                if(state.islogin==false){
                state.login_input_error="账号或密码错误";
                }
            }
        },
        // 注册功能
        register(state,obj){
            if(obj.account=="" || obj.password==""||obj.name=="" || obj.again_password==""){
                state.register_input_error="请不要出现空值";
            }else{
                if(obj.password!=obj.again_password){
                    state.register_input_error="密码不一致";
                }else{
                    state.isregister=true;
                   state.user.push({
                       account:obj.account,
                       username:obj.name,
                       password:obj.password,
                       coupons:0,
                   })

                }
            }
        },
        // 注册初始化
        init_register(state){
            state.isregister=false;
        },
        // 退出登录功能
        isquit(state){
            state.islogin=false;
        },
        // 支付功能
        pay(state,coupons){
            state.user[state.userID].coupons=parseInt(state.user[state.userID].coupons)+parseInt(coupons);
        },
        //第二话扣费
        second_payment(state){
                state.user[state.userID].coupons-=10;
        },
         //第三话扣费
         third_payment(state){
                state.user[state.userID].coupons-=10;
        },
        next(state){
            state.num++;
            if(state.num>3){
                state.num=3;
            }
            if(state.num==2){
                if(state.secondIspay==false){
                    state.user[state.userID].coupons-=10;
                    state.secondIspay=true;
                }
            }
            if(state.num==3){
                if(state.thirdIspay==false){
                    state.user[state.userID].coupons-=10;
                    state.thirdIspay=true;
                }
            }
        },
        prev(state){
            state.num--;
            if(state.num<=0){
                state.num=1;
            }
        },
        init(state){
            state.num=1;
        }
    },
    getters:{},
    actions:{},
}